.NameInput-input{
    width: 70%;
    border: 0 none;
    background: #F5F5F5;
    height: 60px;
    padding: 0 20px;
    box-shadow: 0 0 30px #F5F5F5;
    outline: 0 none;
    margin-top: 10px;
    text-transform: capitalize;
}

.NameInput-button{
    background: #263238;
    color: white;
    border: 0 none;
    outline: 0 none;
    text-transform: uppercase;
    cursor: pointer;
    width: 20%;
    height: 60px;
}

@media screen and (max-width: 1000px){
    .NameInput-input{
        width: 65%;
    }
}