.ListItem-list-item{
    background: #F5F5F5;
    padding: 20px;
    margin: 10px 0;
    width: 92%;
    display: flex;
}

.ListItem-list-item-name{
    flex: 90%;
    margin-top: 5px;
    text-transform: capitalize;
}

.ListItem-list-item-btn{
    flex: 10%;
}

.ListItem-button{
    background: #263238;
    color: white;
    border: 1px solid #263238;
    outline: 0 none;
    cursor: pointer;
    padding: 8px;
}