.NamesApp{
    display: flex;
    flex-flow: row wrap;
}

.NamesApp-input-card{
    flex: 50%;
}

.NamesApp-button{
    background: #263238;
    color: white;
    border: 1px solid #263238;
    outline: 0 none;
    text-transform: uppercase;
    cursor: pointer;
}

.NamesApp-display-card{
    flex: 50%;
    text-align: center;
}

@media screen and (max-width: 1000px){
    .NamesApp-input-card{
        flex: 100%;
    }

    .NamesApp-display-card{
        flex: 100%;
    }

    .NamesApp-input{
        width: 65%;
    }
}