.RandomName-button{
    background: #263238;
    color: white;
    border: 0 none;
    outline: 0 none;
    text-transform: uppercase;
    cursor: pointer;
    width: 60%;
    margin-top: 20px;
    height: 60px;
}

.RandomName-button:disabled{
    background: #AFB1B3;
    cursor: default;
}

.RandomName-text{
    margin: 100px 0;
}

.RandomName-name{
    text-transform: capitalize;
    font-weight: bolder;
    font-size: 32px;
}